import React from "react";
import { Outlet, Link as ReactRouterLink, NavLink } from "react-router-dom";
import { withAuthenticator,
  Text, 
  Card, 
  Link,
  Flex,
  Tabs,
  TabItem, 
  Button, 
  Heading,
  Divider, 
  Collection } from "@aws-amplify/ui-react";
import './App.css';

function App({ signOut, user }) {
  return (
    <>
      <Card variation="elevated">
        <Flex direction={"row"} justifyContent={'space-between'}>
          <Heading level={3}>TXR Platform</Heading>
          <Button onClick={signOut} variation="primary" borderRadius="1rem" size="small">Sign Out</Button>
        </Flex>
      </Card>
      <Flex direction="row" justifyContent="space-around">
        {/* <NavLink className="link-local-styles" to="/arcgis" component={Link}>ArcGIS</NavLink> */}
        <Divider orientation="vertical" />

        {/* <ReactRouterLink className="link-local-styles" to="/unrealengine" component={Link}>Unreal Engine</ReactRouterLink>
        <Divider orientation="vertical" />
        <ReactRouterLink className="link-local-styles" to="/webassembly" component={Link}>Web Assembly</ReactRouterLink> */}


      </Flex>
      {/* <Card variation="elevated" marginTop={'small'} > */}
        <Outlet  />
      {/* </Card> */}
    </>
  );
}

export default withAuthenticator(App);
