import React, { useState } from "react";

const RenderHTML = ({ html, load = true }) => {
  const [loader, setLoader] = useState(load);

  const handleOnLoad = () => {
    setLoader(false);
  };
  return (
    <>
      <iframe src={html} frameBorder="0"  width="100%" height="100%" style={{Margin:"0px" ,padding:"0px"}}></iframe>
      {/* {loader && <div className="holds-the-iframe"></div>} */}

    
    </>
  );
};

export default RenderHTML;
