import React from 'react';
import ReactDOM from 'react-dom/client';
import {
  BrowserRouter,
  Routes,
  Route,
} from "react-router-dom";
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';

import Arcgis from './pages/Arcgis';
import UnrealEngine from './pages/UnrealEngine';
import WebAssembly from './pages/WebAssembly';

import "@aws-amplify/ui-react/styles.css";
import { Amplify } from 'aws-amplify';
import awsExports from './aws-exports';
Amplify.configure(awsExports);

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Arcgis />} >
          <Route path="/" element={<Arcgis />} />
          <Route path="/unrealengine" element={<UnrealEngine />} />
          <Route path="/webassembly" element={<WebAssembly />} />
        </Route>
      </Routes>
    </BrowserRouter>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
