import RenderHTML from "../Components/RenderHtml";

const Arcgis = () => {
    const [index, setIndex] = useState(0);

    const handleTabChange = (i) => {
        console.log('Upcoming tab:', i);
        console.log('Current tab:', index);
        setIndex(i);
    }
    const renderSelectedTab = () => {
        if (index == 0) { //Simple Widgets
            return <RenderHTML html="html/simpleWidgets.html" />;
        } else if(index == 1) { //Time Slider
            return <RenderHTML html="html/timeSlider.html" />;
        }else if(index == 2) { //Mesh Layer
            return <RenderHTML html="html/integratedMesh.html" />;
        } else if(index == 3) { //Part Planning
            return <RenderHTML html="https://staging.d3g1oqscnv1c16.amplifyapp.com/" />;
        } else if(index == 4) { //BIM
            return <RenderHTML html="html/bim2.html" />;
        } else if(index == 5) {
            return <RenderHTML html="html/citiesFootprint.html" />;
        } else if (index == 6) {
            return <RenderHTML html="html/cities3D.html" />;
        } else if(index == 7) {
            console.log(index)
            return <RenderHTML html="html/slideTour.html" />;
        } else if(index == 8) {
            console.log(index)
            return <RenderHTML html="html/RealEstate/realEstate.html" />;

        }
        else {
            return <RenderHTML html="" />

        }
    }
    return (
        <>

                {renderSelectedTab()}




</>
    );
}

export default Arcgis;
